import React from "react";
import Header from "./komponen/Header";
import Artikel from "./komponen/Artikel"
import Footer from "./komponen/Footer"

function App() {
  const [makanans, setMakanans] = React.useState([{
    nama_makanan: "rendang",
    image:
      "https://asset.kompas.com/crops/LNKP5s429gtSsDa5Z6KODVcNlq0=/100x67:900x600/750x500/data/photo/2020/06/30/5efb2f520efb4.jpg",
    harga: "Rp 25000",
    rating: 5,
  },
  {
    nama_makanan: "bakso",
    image:
      "https://blue.kumparan.com/image/upload/fl_progressive,fl_lossy,c_fill,q_auto:best,w_640/v1609055451/f2btmxezynqjw8cowz4n.jpg",
    harga: "Rp 20000",
    rating: 5,
  },
  {
    nama_makanan: "Ayam bakar Jonggol",
    image:
      "https://www.resepistimewa.com/wp-content/uploads/ayam-bakar-spesial.jpg",
    harga: "Rp 30000",
    rating: 5,
  }
]);


  return (
    <React.Fragment>
      <div className="antialiased relative text-gray-600 pb-72">
        <div className=" w-full min-h-screen">
          <Header className="absolute"></Header>
        </div>
        <section id="services" className=" bg-gray-50 pt-20 pb-28 px-8 mb-72">
          <div className="max-w-6xl mx-auto">
            <div className="text-center">
              <h1 className="text-6xl font-bold text-gray-700">Foods</h1>
              <p className="pt-2 text-xl">Here What I Offer</p>
            </div>
            <div className="mt-24 ">
              <div className="relative grid grid-cols-3 gap-20">
                {makanans.map((makanan, index) => (
              <Artikel 
                key={index}
                nama_makanan={makanan.nama_makanan}
                image={makanan.image}
                harga={makanan.harga}
                rating={makanan.rating}
              ></Artikel>
            ))}
              </div>
            </div>
          </div>
        </section>
        <Footer></Footer>
      </div>
    </React.Fragment>
  );
}

export default App;
