import React from "react";
import BackgroundImage from "../assets/1.jpg";
import photoProfile from "../assets/pp.png";

function Header(props) {
  return (
      
    <header className="h-full w-full flex ">
       
      <div
        className="absolute top-0 w-full h-1/4 bg-cover bg-bottom"
        style={{ backgroundImage: `url(${BackgroundImage})` }}
      >
        
      </div>
         
      <div className="relative z-10 flex justify-center items-center mx-auto min-h-screen h-auto">
        <div className="max-w-4xl relative">
          <div className="absolute hidden sm:block inset-0 transform -skew-x-4 rounded-lg z-10 -rotate-6 w-full max-w-4xl p-12 shadow-xl bg-gradient-to-r from-yellow-600 to-red-500"></div>
          <div className="relative z-20 flex flex-col lg:flex-row bg-white justify-between w-full max-w-4xl p-12 shadow-xl rounded-lg">
            <div className="flex flex-col space-y-6 py-6  justify-between order-2">
              <div>
                <h2 className="text-lg">Hello I AM</h2>
                <h1 className="text-4xl font-bold text-gray-800">ROFL1ST</h1>
              </div>
              <p className="text-md leading-relaxed">
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Iste,
                ea excepturi. Magnam libero natus laudantium sint ad quas
                laboriosam labore!
              </p>
            </div>
            <img
              className="w-24 lg:w-64 h-24 lg:h-64 rounded-full border-white shadow-lg flex-shrink-0 order-1 lg:order-2"
              src={photoProfile}
              alt=""
            />
          </div>
        </div>
      </div>
    </header>
  );
}

export default Header;
